/* ===== ESTILO M√âDICO PROFESIONAL RESPONSIVE ===== */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f9fa;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

/* ===== CONTENEDORES ===== */
.login-container,
.register-container,
.presentacion-card {
  background: #ffffff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 100%;
  max-width: 400px;
  animation: fadeIn 0.8s ease-in-out;
  box-sizing: border-box;
}

/* ===== T√çTULOS ===== */
h1, h2 {
  color: #00796B;
  margin-bottom: 20px;
  font-weight: 600;
}

/* ===== INPUTS ===== */
input, select {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 1em;
  transition: all 0.3s;
  box-sizing: border-box;
}

input:focus, select:focus {
  outline: none;
  border-color: #00BFA6;
  box-shadow: 0 0 5px rgba(0,191,166,0.3);
}

/* ===== BOTONES ===== */
button, .btn-primary {
  width: 100%;
  padding: 12px;
  background: #00BFA6;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1.1em;
  cursor: pointer;
  transition: background 0.3s;
  margin-top: 10px;
}

button:hover, .btn-primary:hover {
  background: #009e8e;
}

.secondary-btn {
  background: #e0f2f1;
  color: #00796B;
  font-weight: bold;
}

.secondary-btn:hover {
  background: #b2dfdb;
}

/* ===== TEXTOS Y ENLACES ===== */
.extra-info {
  margin-top: 15px;
  font-size: 0.9em;
  color: #555;
}

.extra-info a {
  color: #00796B;
  text-decoration: none;
  font-weight: bold;
}

.extra-info a:hover {
  text-decoration: underline;
}

/* ===== ANIMACI√ìN ===== */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* =============================== */
/* üì± DISE√ëO RESPONSIVE */
/* =============================== */

/* üî∏ Tablets (pantallas medianas) */
@media (max-width: 992px) {
  body {
    padding: 15px;
  }

  .login-container,
  .register-container,
  .presentacion-card {
    max-width: 350px;
    padding: 30px;
  }

  h1, h2 {
    font-size: 1.4em;
  }
}

/* üî∏ Celulares (pantallas peque√±as) */
@media (max-width: 600px) {
  body {
    flex-direction: column;
    padding: 10px;
  }

  .login-container,
  .register-container,
  .presentacion-card {
    width: 100%;
    max-width: 95%;
    padding: 20px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  }

  h1, h2 {
    font-size: 1.2em;
  }

  input, button {
    font-size: 0.95em;
    padding: 10px;
  }

  .extra-info {
    font-size: 0.85em;
  }
}
